<template>
  <div class="hello">
    <div style="clear: both">
      <ul>
        <li
          class="it"
          :class="{ showAll_k: showAll_k == k, active: k == aIndex }"
          v-for="(it, k) in items"
          :key="it.aid"
        >
          <img
            class="coverUrl"
            :src="it.coverUrl"
            referrerPolicy="no-referrer"
            @click="showAll_k = showAll_k == k ? -1 : k"
          />
          <div>
            <div>
              <b>#{{ k }}</b
              >{{ it.title || it.aid }}:{{ it.aid }}:({{ it.items.length }})
              <span
                v-if="$store.state.showEdit"
                style="cursor: pointer"
                @click="del(k, -1)"
                >x</span
              >
            </div>
            <div>
              <li v-for="(item, i) in it.items" :key="item.path">
                <span
                  :class="{ cur: aIndex == k && bIndex == i }"
                  @click="play(k, i)"
                  class="url"
                  :title="item.title"
                  >{{ it.title && it.title != "dy" ? i + 1 : item.title }}</span
                >
                <em v-if="isPreview" class="url" @click="openUrl(k, i)">*</em>
                <em v-if="$store.state.showEdit" class="url" @click="del(k, i)"
                  >x</em
                >
              </li>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  components: {},
  name: "Main",
  data: () => {
    return {
      isPlaying: false,
      curPosition: 0,
      percent: 0,
      duration: 0,
      items: [],
      progress: 0,
      updateRemote: 0,
      showAll_k: -1,
      isEdit: false,
      isPreview: false,
    };
  },
  computed: {
    aIndex() {
      return this.$store.state.playController.aIndex;
    },
    bIndex() {
      return this.$store.state.playController.bIndex;
    },
  },
  activated: function () {
    this.getDatas();
  },
  props: {
    msg: String,
  },
  methods: {
    ...mapActions(["updateStatus"]),
    updateProgress(percent, isPlay) {
      this.curPosition = percent * this.duration;
      this.isPlaying = isPlay;
      this.updateRemote++;
    },
    openUrl(aIndex, bindex) {
      open("/api/url?aIndex=" + aIndex + "&bIndex=" + bindex);
    },
    del(aIndex, bindex) {
      confirm("delete it?") &&
        fetch("/api/delete?aIndex=" + aIndex + "&bIndex=" + bindex, {
          method: "GET",
        }).then((res) => {
          this.getDatas();
        });
    },
    base64(item) {
      let n = "data:image/png;base64," + item.thumb;
      console.log(n);
      return n;
    },
    play(aIndex, bIndex) {
      this.$store.dispatch("cmdAction", {
        cmd: "play",
        aIndex: aIndex,
        bIndex: bIndex,
        typeId: 0,
      });
    },

    getDatas() {
      fetch("/api/list.json", {
        method: "GET",
        mode: "cors",
        credentials: "include",
      })
        .then((response) => {
          response.json().then((data) => {
            this.items.length = 0;
            this.items.push(...data);
          });
        })
        .catch(function (e) {
          console.log("error: " + e.toString());
        });
    },
  },
  watch: {},
};
</script>

