<template>
  <div>
    <header
      style="
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        height: 90px;
        background: rgba(255, 255, 255, 0.8);
        z-index: 1000;
      "
    >
      <h4 style="margin: 5px">
        宝贝计划
        <span style="position: relative; display: inline-block">
          <div
            style="
              position: fixed;
              background: white;
              font-weight: normal;
              font-size: 14px;
              right: 0;
              top: 0;
            "
          >
            <ul
              v-show="showMenu"
              style="margin: 0; padding: 0; border: 1px solid; padding: 10px"
            >
              <li><input type="checkbox" v-model="showEdit" />显示管理按钮</li>
            </ul>
            <font-awesome-icon
              icon="cog"
              fixed-width
              style="position: absolute; right: 0; top: 10px"
              @click="showMenu = !showMenu"
            />
          </div>
        </span>
      </h4>

      <div style="clear: both">
        <ul>
          <li
            class="tab"
            v-for="(tab, i) in tabs"
            @click="curTabIndex = i"
            :key="i"
            :class="{ curTab: curTabIndex == i }"
          >
            {{ tab.name }}
          </li>
        </ul>
      </div>
    </header>
    <div style="margin-top: 110px">
      <keep-alive>
        <component :is="tabs[curTabIndex].comp"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import ByVideo from "./ByVideo";
import ByImage from "./ByImage";
import ByAudio from "./ByAudio";
import ByWhen from "./ByWhen";
import GameList from "./GameList";
import EditWords from "./EditWords";

export default {
  components: { ByVideo, ByImage, ByAudio, ByWhen, GameList, EditWords },
  data: () => {
    return {
      curTabIndex: 1,
      showMenu: 0,
      tabs: [
        { name: "视频学习", comp: "ByVideo" },
        { name: "看图识字", comp: "ByImage" },
        { name: "音乐", comp: "ByAudio" },
        { name: "考考你", comp: "ByWhen" },
        { name: "EditWords", comp: "EditWords" },
      ],
    };
  },
  computed: {
    showEdit: {
      get() {
        return this.$store.state.showEdit;
      },
      set(value) {
        this.$store.commit("showEdit", value);
      },
    },
  },
};
</script>

