.tab {
  cursor: pointer;
}
.curTab {
  background: white;
}

.curTab:after {
  content: "";
  width: auto;
  min-width: 44px;
  height: 2px;
  background: #4e6ef2;
  border-radius: 1px;
  display: block;
  margin-top: 1px;
}
